}
.footer .form .ic1 {
  margin-top: 40px;
}
.footer .form .ic2 {
  position: absolute;
  margin-top: 30px;
}
.footer .form .input {
  border: 0;
  box-sizing: border-box;
  color: #eee;
  font-size: 18px;
  height: 100%;
  outline: 0;
  padding: 4px 20px 0;
  width: 60%;
  background: #FFFFFF;
  box-shadow: 2px 3px 6px rgba(7, 4, 59, 0.157043);
  border-radius: 24px;
}
.footer .form .cut {
  background-color: #15172b;
  border-radius: 10px;
  height: 20px;
  left: 20px;
  position: absolute;
  top: -20px;
  transform: translateY(0);
  transition: transform 200ms;
  width: 76px;
}
.footer .form .cut-short {
  width: 50px;
}
.footer .form .input:focus ~ .cut,
.footer .form .input:not(:placeholder-shown) ~ .cut {
  transform: translateY(8px);
}
.footer .form .placeholder {
  color: #65657b;
  font-family: sans-serif;
  left: 20px;
  line-height: 14px;
  pointer-events: none;
  position: absolute;
  transform-origin: 0 50%;
  transition: transform 200ms, color 200ms;
  top: 20px;
}
.footer .form .input:focus ~ .placeholder,
.footer .form .input:not(:placeholder-shown) ~ .placeholder {
  transform: translateY(-30px) translateX(10px) scale(0.75);
}
.footer .form .input:not(:placeholder-shown) ~ .placeholder {
  color: #808097;
}
.footer .form .input:focus ~ .placeholder {
  color: #dc2f55;
}
.footer .form .submit {
