.tile {

    .tile__wrapper {

        .tile__container {
            display: flex;
            align-items: center;
            justify-content: space-between;

            .tile__image {
                max-width: 614px;
                width: 100%;
                height: auto;

                img {
                    width: 100%;
                    height: 100%;
                }
            }

            .tile__texts {
                max-width: 563px;

                h1 {
                    margin-bottom: 19px;
                }

                .tile__links {
                    margin-top: 24px;
                    position: relative;
                    display: flex;
                    align-items: center;
                    gap: 8px;
                    width: max-content;

                    a {
                        color: #62E0D9;
                    }

                    &::after {
                        content: '';
                        position: absolute;
                        bottom: 0;
                        left: 0;
                        width: 100%;
                        height: 1px;
                        background: #62E0D9;
                    }

                    &:hover {
                        a {
                            color: #FFFFFF;
                        }

                        &::after {
                            background: #FFFFFF;
                        }

                        img {
                            filter: grayscale(1);
                        }
                    }
                }
            }
        }
    }
}


@media only screen and (max-width: 768px) {
    .tile {

        .tile__wrapper {
    
            .tile__container {
                flex-direction: column;
    
                .tile__image {
                    max-width: 614px;
                    width: 100%;
                    height: auto;
    
                    img {
                        width: 100%;
                        height: 100%;
                    }
                }
    
                .tile__texts {
                    max-width: 563px;
    
                    h1 {
                        margin-bottom: 19px;
                    }
    
                    .tile__links {
                        margin-top: 24px;
                        position: relative;
                        display: flex;
                        align-items: center;
                        gap: 8px;
                        width: max-content;
    
                        a {
                            color: #62E0D9;
                        }
    
                        &::after {
                            content: '';
                            position: absolute;
                            bottom: 0;
                            left: 0;
                            width: 100%;
                            height: 1px;
                            background: #62E0D9;
                        }
    
                        &:hover {
                            a {
                                color: #FFFFFF;
                            }
    
                            &::after {
                                background: #FFFFFF;
                            }
    
                            img {
                                filter: grayscale(1);
                            }
                        }
                    }
                }
            }
        }
    }
}